const tableFields = [
  {
    displayName: 'Order ID',
    key: 'orderNumber',
  },
  {
    displayName: 'User Name',
    key: 'userName',
  },
  {
    displayName: 'Vendor Name',
    key: 'brandName',
  },
  {
    displayName: 'Vendor Rating',
    key: 'rating',
    className: 'active-green',
    isRating: true,
    ratingType: 'star',
  },
  // {
  //   displayName: 'Taste',
  //   key: 'taste',
  //   isRating: true,
  // },
  // {
  //   displayName: 'Temperature',
  //   key: 'temperature',
  //   isRating: true,
  // },
  {
    displayName: 'Packaging',
    key: 'packaging',
    isRating: true,
  },
  {
    displayName: 'Price Value',
    key: 'priceValue',
    isRating: true,
  },
  {
    displayName: 'Vendor Review',
    key: 'review',
  },
  {
    displayName: 'Logistics Driver Name',
    key: 'driverName',
  },
  {
    displayName: 'Logistics Rating',
    key: 'logisticRating',
    className: 'active-green',
    isRating: true,
    ratingType: 'star',
  },
  {
    displayName: 'Time',
    key: 'logisticTime',
    isRating: true,
  },
  {
    displayName: 'Food handling',
    key: 'logisticFoodHandling',
    isRating: true,
  },
  {
    displayName: 'Professionalism',
    key: 'logisticProfessionalism',
    isRating: true,
  },
  {
    displayName: 'Logistics Review',
    key: 'logisticReview',
  },
];
// eslint-disable-next-line no-unused-vars
const RatingManagement = props => {
  const [mounted, setMounted] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);
  const {
    logisticsReviews: { list: allData, totalRecords },
    errorMsg,
  } = useSelector(state => state.review);

  const dispatch = useDispatch();

  const getData = (columnName, orderBy, search, skip, limit) => {
    if (search && skip !== 0) {
      dispatch(
        getAllLogisticsRatingReviewSaga({
          columnName,
          orderBy,
          search,
          skip: 0,
          limit,
        }),
      );
    } else {
      dispatch(
        getAllLogisticsRatingReviewSaga({
          columnName,
          orderBy,
          search,
          skip,
          limit,
        }),
      );
    }
  };

  useEffect(() => {
    setMounted(true);
    getData('orderNumber', 'desc', '', 0, 10);
    return () => setMounted(false);
  }, []);

  // const statusAction = data => {
  //   dispatch(editBlockChefStatusSaga({ id: data.id, isBlock: data.status }));
  // };

  const linkObj = {
    // view: '/chefs/chef-detail',
  };

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>Rating</h2>
              </div>

              <div className="coman-table" id="owner-detail-btn">
                <Table
                  headers={tableFields}
                  tableData={allData || null}
                  pageCount={totalRecords?.count}
                  getData={getData}
                  link={linkObj}
                  linkPropsName="_id"
                  // statusAction={statusAction}
                  setSearchFlag={searchFlagSetter}
                  errorMsg={errorMsg || 'Loading...'}
                  initialSortColumnName="orderNumber"
                  initialSortOrderBy="desc"
                // pageSizeProp={pageSize}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SetTokenHeader(RatingManagement, axios);
